import React from 'react'
import { Autocomplete, TextField, Box } from '@mui/material';

export default function AutoComplete({ options, setValues, values }) {
    return (

        <>
            <Autocomplete

                getOptionLabel={(option) => option.name}

                onChange={(e, newValue) => {
                    setValues({ ...values, ["country"]:newValue.name })
                    console.log(newValue);

                }}
                disablePortal
                className=''
                options={options}
                renderInput={
                    prams =>
                        <TextField label="Country" {...prams} />

                }
                renderOption={
                    (props, option) =>

                        <Box component="li" {...props}>
                            {/* <img className='m-2 shrink-0' loading='lazy' src={`https://flagcdn.com/w20/${option.code.toLowerCase()}.png`}
                            srcSet={`https://flagcdn.com/w40/${option.code.toLowerCase()}.png 2x`}
                            alt="" />  */}
                            {/* {option} ({option.code}  ) +{option.phone} */}

                            {option.name}



                        </Box>

                }
            />

        </>

    )
}
